.social-login {
  display: flex;
  align-items: center;
  button {
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 5px;
    height: 45px;
    width: 300px;
    margin: 10px;
    font-size: 16px;

    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    &:not(.social-login--kakaotalk) {
      a {
        text-decoration: none;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      i {
        font-size: 26px;
        margin-right: 5px;
      }
    }
    &:hover {
      opacity: 0.9;
    }
    &:active {
      opacity: 0.4;
    }
    &.social-login--github {
      background-color: rgb(199, 199, 199);
      a {
        color: black;
      }
    }
    &.social-login--facebook {
      background-color: #415e9a;
      a {
        color: white;
      }
    }
    &.social-login--kakaotalk {
      background-color: rgba(255, 255, 255, 0);
      padding: 0;
    }
  }
}

@media screen and (max-device-width: 800px) {
  .social-login {
    margin-top: 50px;
    flex-direction: column;
    button {
      height: 90px;
      width: 60vw;
      font-size: 32px;
      &:not(.social-login--kakaotalk) {
        i {
          font-size: 38px;
        }
      }
      &.social-login--facebook {
        a {
          text-decoration-line: line-through;
        }
      }
      img {
        height: 90px;
        width: 60vw;
      }
    }
  }
}
