.user-profile {
  display: flex;
  flex-direction: column;
  align-items: center;

  .user-profile__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
    .profile__title {
      border-bottom: 1px solid $borderBottom;
      margin-bottom: 30px;
      padding-bottom: 10px;
      width: 250px;
      display: flex;
      justify-content: center;

      h3 {
        font-size: 32px;
      }
    }
    .profile__image {
      width: 150px;
      height: 150px;
      margin-bottom: 30px;
      img {
        width: inherit;
        height: inherit;
        border-radius: 30%;
      }
    }
    .profile__username,
    .profile__email {
      border-bottom: 1px solid $borderBottom;
      padding-bottom: 5px;
      margin-bottom: 30px;
      h4 {
        font-size: 23px;
      }
    }
  }
  .profile__buttons {
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-bottom: 20px;

    a {
      &.profile__edit {
        @include button1(gray);
      }
      &.profile__changePassword {
        @include button1(gray);
      }
      &.profile__delete {
        @include button1(red);
      }
    }
  }
}

@media screen and (max-device-width: 480px) {
  .user-profile {
    .user-profile__header {
      width: 100%;
      height: 50vh;
      .profile__title {
        width: initial;
        h3 {
          font-size: 64px;
        }
      }
      .profile__image {
        width: 300px;
        height: 300px;
        margin-bottom: 30px;
        img {
          width: inherit;
          height: inherit;
          border-radius: 30%;
        }
      }
      .profile__username,
      .profile__email {
        border-bottom: 1px solid $borderBottom;
        padding-bottom: 5px;
        margin-bottom: 30px;
        h4 {
          font-size: 40px;
        }
      }
    }
    .profile__buttons {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      align-items: center;
      justify-items: center;
      margin-bottom: 20px;
      margin-top: 200px;

      a {
        &.profile__edit {
          @include button1(gray);
        }
        &.profile__changePassword {
          @include button1(gray);
        }
        &.profile__delete {
          @include button1(red);
        }
      }
    }
  }
}
